body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffb347;
  color: #003866;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-weight: bold;
}

.navbar {
  background-color: #003866;
  margin-bottom: 15px;
}

#basic-navbar-nav .nav-link {
  color: #ffb347;
}

.dropdown-menu {
  background-color: #ffb347;
}

.dropdown-item {
  color: #003866;
}

.dropdown-item:hover {
  background-color: #003866;
  color: #ffb347;
}

.resume {
  display: inline-block;
}

.home p {
  font-size: large;
  font-weight: 500;
  padding: 10px;
}

.home {
  display: inline-block;
  width: 65%;
}

.resume {
  display: inline-block;
  max-width: 100%;
}

.resume h1 {
  display: inline-block;
  width: 100%;
}

.resume a {
  color: #003866;
}
.resume .link {
  margin-bottom: 15px;
}

.about {
  display: inline-block;
  max-width: 75%;
}

.navbar .navbar-brand {
  color: #ffb347;
  font-weight: bold;
}

.profile-pic {
  max-height: 400px;
}

.about p {
  margin: 20px;
  text-align: left;
  display: inline-block;
  width: 90%;
}

.about p a {
  color: #003866;
  font-weight: 700;
  font-style: italic;
}